"use strict";

var React = React;
var BrowserTreeNode = BrowserTreeNode;
var ctx = ctx;

var BrowserFolderTree = React.createClass({
	displayName: "BrowserFolderTree",

	propTypes: {
		selectedFolder: React.PropTypes.string.isRequired,
		onSelectedChanged: React.PropTypes.func.isRequired
	},
	getInitialState: function getInitialState() {
		return null;
	},
	componentDidMount: function componentDidMount() {
		var _this = this;

		ctx.getFolderTree().then(function (data) {
			return _this.setState(data);
		});
	},
	render: function render() {

		var tree;

		if (this.state) {
			var tree = React.createElement(
				"ul",
				{ className: "tree-container" },
				React.createElement(BrowserTreeNode, { key: this.state.id,
					folder: this.state,
					selectedFolder: this.props.selectedFolder,
					onSelectedChanged: this.props.onSelectedChanged })
			);
		}

		return React.createElement(
			"div",
			{ className: "browser-folders tree" },
			tree
		);
	}
});